
.movie_wrapper {
  width: 800px;
  margin: 0 auto;
  padding-top: 100px;
  margin-bottom: 20px;
}
.movie_wrapper .info_wrapper ul {
  display: inline-block;
  height: 400px;
  vertical-align: top;
  margin-left: 40px;
}
.movie_wrapper .info_wrapper ul li {
  height: 50px;
  line-height: 50px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #37a;
  font-size: 18px;
}
.movie_wrapper .info_wrapper ul li span {
  color: #999;
  font-size: 20px;
}
.movie_wrapper .comment_wrapper {
  border: 1px solid rgba(0,0,0,0.2);
  box-shadow: 0 0 2px rgba(0,0,0,0.2);
  padding: 20px 20px;
  margin-top: 20px;
}
.movie_wrapper .comment_wrapper h3 {
  font-size: 24px;
  margin-bottom: 15px;
  color: #017e66;
  font-weight: bold;
}
.movie_wrapper .comment_wrapper .comment_item {
  border-top: 1px solid #e5e9ef;
  position: relative;
  padding: 20px 0;
  color: #333;
  font-size: 18px;
}
.movie_wrapper .comment_wrapper .comment_item .user {
  color: #017e66;
  margin-right: 8px;
  font-weight: bold;
  vertical-align: top;
  display: inline-block;
}
.movie_wrapper .comment_wrapper .comment_item .date {
  position: absolute;
  right: 0;
  font-size: 13px;
  color: #666;
}
.movie_wrapper .comment_wrapper .comment_item .comment {
  display: inline-block;
  word-wrap: break-word;
  word-break: break-all;
  width: 500px;
}
.movie_wrapper .comment_wrapper .no_comment {
  text-align: center;
  font-size: 16px;
  color: rgba(0,0,0,0.6);
}
.movie_wrapper .input_wrapper {
  margin-top: 20px;
  border: 1px solid rgba(0,0,0,0.2);
  box-shadow: 0 0 2px rgba(0,0,0,0.2);
  position: relative;
}
.movie_wrapper .input_wrapper .input {
  width: 100%;
}
.movie_wrapper .input_wrapper .input .content {
  display: block;
  margin: 20px auto;
  resize: none;
  border: 2px solid rgba(1,126,102,0.6);
  width: 750px;
  font-size: 16px;
  height: 200px;
  overflow: auto;
  word-break: break-all;
}
.movie_wrapper .input_wrapper .input .submit_btn {
  display: block;
  border-radius: 4px;
  cursor: pointer;
  width: 100px;
  height: 40px;
  background-color: rgba(1,126,102,0.6);
  color: #fff;
  margin: 20px auto;
}
.movie_wrapper .input_wrapper .lockedInfo {
  width: 120px;
  height: 50px;
  line-height: 50px;
  background-color: rgba(1,126,102,0.6);
  color: #fff;
  font-size: 20px;
  text-align: center;
  border-radius: 4px;
  position: absolute;
  left: 50%;
  margin-left: -60px;
  top: 95px;
}
.movie_wrapper .input_wrapper .word_limit {
  font-size: 18px;
  color: rgba(0,0,0,0.6);
  position: absolute;
  bottom: 60px;
  right: 20px;
}


