
.bar_wrapper {
  height: 40px;
  line-height: 40px;
  margin: 20px auto;
}
.bar_wrapper span {
  border: 1px solid rgba(0,0,0,0.2);
  box-shadow: 0 0 1px rgba(0,0,0,0.2);
  border-radius: 4px;
  display: inline-block;
  width: 40px;
  height: 40px;
  text-align: center;
  margin-right: 8px;
  cursor: pointer;
  color: #666;
}
.bar_wrapper .large_btn {
  width: 60px;
}
.bar_wrapper .on {
  background-color: rgba(1,126,102,0.6);
  color: #fff;
  border: none;
  box-shadow: 0 0 0;
}


